.snapshot-notice--box {
	background-color: #FFFFFF;
	border-radius: 4px;
	box-shadow: 0 2px 0 #E6E6E6;
    display: flex;
	margin-bottom: 15px;
	min-height: 20px;
	padding: 20px 35px;
    position: relative;

	&.notice {
		border: none;
	}

	* {
		box-sizing: border-box;
	}

	.snapshot-no--spacing {
		margin-block-start: 0 !important;
		margin-block-end: 0 !important;
	}

	.snapshot-button{
		cursor: pointer;
		display: inline-block;
		position: relative;
		margin: 0;
		border-width: 2px;
		border-style: solid;
		border-color: transparent;
		border-radius: 4px;
		text-decoration: none;
		text-align: center;
		transition: all 0.3sease;
		width: auto;
		min-width: 80px;
		padding: 5px 14px;
		color: #FFFFFF;
		font: 500 12px / 16px "Roboto", Arial, sans-serif;
		letter-spacing: -0.25px;
		text-transform: uppercase;

		&.snapshot-button--blue {
			background-color: #17A8E3;
    		color: #FFFFFF;
		}

		&.snapshot-button--light__gray {
			background-color: rgb(248, 248, 248);
    		color: rgb(136, 136, 136);
		}
	}

	.snapshot-box--image__space {
		margin-right: 30px;
	}

	.snapshot-summary--segment {
		h3 {
			font-size: 18px;
			line-height: 40px;
			display: block;
			margin: 0.5em auto;
			padding: 0;
			color: #333333;
			font-weight: 700;
			font-family: "Roboto", Arial, sans-serif;
		}

		.snapshot-block-text {
			margin-bottom: 10px;
			font-family: "Roboto", Arial, sans-serif;
			font-weight: 400;
			font-size: 15px;
			line-height: 30px;
			color: #666666;
			letter-spacing: -0.25px;
		}
	}


}